import { AvatarLoadProps } from './AvatarLoadProps'
import { EventEmitter } from './utils/eventBus'

export abstract class AvatarService extends EventEmitter {
  debug = false
  abstract getModelFiles(): File[]
  abstract loadAvatar(props: AvatarLoadProps): Promise<void>
  abstract init(canvas: HTMLCanvasElement): Promise<void>
  abstract destroy(): void
  abstract applyAvatarBlendShapeValues(values: { [key: string]: number }): void
  abstract setAvatarPosition(x: number, y: number): void
  abstract setAvatarScale(scale: number): void
  abstract setAvatarRotation(rotation: number): void
  abstract setAvatarEmotion(emotion: string): void
  abstract getAvatarAllEmotion(): string[] | undefined
  abstract resetAvatarEmotion(): void
  abstract getAvatarAllParameters(): string[]
}

export class CanvasResizer {
  resizer: ResizeObserver | undefined
  constructor(
    canvas: HTMLCanvasElement,
    onResize: (width: number, height: number) => void,
  ) {
    const resizedw = () => {
      const parentElement = canvas.parentElement

      if (!parentElement) return

      // if the size is the same dont change
      const rect = parentElement.getBoundingClientRect()

      onResize(rect.width, rect.height)
    }

    let doit: NodeJS.Timeout
    this.resizer = new ResizeObserver(() => {
      clearTimeout(doit)
      doit = setTimeout(resizedw, 10)
    })

    this.resizer.observe(canvas.parentElement!)

    resizedw()
  }

  destroy(): void {
    this.resizer?.disconnect()
    this.resizer = undefined
  }
}
